'use client'
import React, { useState } from 'react';
import { Building2, User, MapPin, Phone, Mail, FileText, Percent, Power } from 'lucide-react';

interface FormData {
    name: string;
    accNo: string;
    address: string;
    location: string;
    post: string;
    pin: string;
    phone: string;
    whatsapp: string;
    email: string;
    type: string;
    gstNo: string;
    discPercentage: string;
    stopped: string;
    status: string;
}

interface FormErrors {
    [key: string]: string;
}

interface TypeOption {
    id: number;
    name: string;
}

const SupplierForm = () => {
    const [formData, setFormData] = useState<FormData>({
        name: '',
        accNo: '',
        address: '',
        location: '',
        post: '',
        pin: '',
        phone: '',
        whatsapp: '',
        email: '',
        type: '',
        gstNo: '',
        discPercentage: '',
        stopped: 'N',
        status: '1'
    });

    const [errors, setErrors] = useState<FormErrors>({});
    const [isSubmitting, setIsSubmitting] = useState<boolean>(false);

    const [showTypeModal, setShowTypeModal] = useState(false);
    const [newType, setNewType] = useState('');
    const [types, setTypes] = useState<TypeOption[]>([]);



    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
        const { name, value } = e.target;
        setFormData(prev => ({ ...prev, [name]: value }));

        // Clear error when user starts typing
        if (errors[name]) {
            setErrors(prev => ({ ...prev, [name]: '' }));
        }
    };

    const validateForm = (): boolean => {
        const newErrors: FormErrors = {};

        if (!formData.name.trim()) newErrors.name = 'Supplier name is required';
        if (!formData.accNo.trim()) newErrors.accNo = 'Account number is required';
        if (!formData.address.trim()) newErrors.address = 'Address is required';
        if (!formData.location.trim()) newErrors.location = 'Location is required';
        if (!formData.pin.trim()) newErrors.pin = 'PIN code is required';
        if (!formData.phone.trim()) newErrors.phone = 'Phone number is required';
        if (!formData.type) newErrors.type = 'Supplier type is required';

        // Email validation
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        if (formData.email && !emailRegex.test(formData.email)) {
            newErrors.email = 'Please enter a valid email address';
        }

        // Phone validation
        const phoneRegex = /^\d{10}$/;
        if (formData.phone && !phoneRegex.test(formData.phone.replace(/\D/g, ''))) {
            newErrors.phone = 'Please enter a valid 10-digit phone number';
        }

        // GST validation
        const gstRegex = /^[0-9]{2}[A-Z]{5}[0-9]{4}[A-Z]{1}[1-9A-Z]{1}Z[0-9A-Z]{1}$/;
        if (formData.gstNo && !gstRegex.test(formData.gstNo)) {
            newErrors.gstNo = 'Please enter a valid GST number';
        }

        // PIN validation
        const pinRegex = /^\d{6}$/;
        if (formData.pin && !pinRegex.test(formData.pin)) {
            newErrors.pin = 'Please enter a valid 6-digit PIN code';
        }

        setErrors(newErrors);
        return Object.keys(newErrors).length === 0;
    };

    const fetchTypes = async () => {
        try {
            const response = await fetch('https://localhost:7112/api/MedicineTypes');
            const data: { typeId: number; typeName: string }[] = await response.json();

            setTypes(data.map(t => ({
                id: t.typeId,
                name: t.typeName
            })));
        } catch (error) {
            console.error('Error fetching types:', error);
        }
    };

    const handleAddType = async () => {
        if (newType.trim()) {
            try {
                const response = await fetch('https://localhost:7112/api/MedicineTypes', {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ typeName: newType.trim() })
                });

                if (response.ok) {
                    const newTypeData = await response.json();

                    const newEntry = {
                        id: newTypeData.typeId,
                        name: newTypeData.typeName
                    };

                    await fetchTypes(); // âœ… Refresh type list

                    setFormData(prev => ({
                        ...prev,
                        typeId: newEntry.id,
                        type: newEntry.name
                    }));

                    setNewType('');
                    setShowTypeModal(false);
                } else {
                    const errText = await response.text();
                    console.error('Failed to add type:', errText);
                }
            } catch (error) {
                console.error('Error adding type:', error);
            }
        }
    };

    const handleSubmit = async (): Promise<void> => {

        if (!validateForm()) return;

        setIsSubmitting(true);

        try {
            // Simulate API call
            await new Promise(resolve => setTimeout(resolve, 1500));

            // Here you would typically send data to your backend
            console.log('Form submitted:', formData);

            // Reset form on success
            setFormData({
                name: '',
                accNo: '',
                address: '',
                location: '',
                post: '',
                pin: '',
                phone: '',
                whatsapp: '',
                email: '',
                type: '',
                gstNo: '',
                discPercentage: '',
                stopped: 'N',
                status: '1'
            });

            alert('Supplier registered successfully!');
        } catch (error) {
            console.error('Error submitting form:', error);
            alert('Error submitting form. Please try again.');
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <div className="min-h-screen bg-gradient-to-br from-blue-50 via-white to-indigo-50 py-8 px-4">
            <div className="max-w-4xl mx-auto">
                {/* Form Container */}
                <div className="bg-white rounded-2xl shadow-xl border border-gray-100 overflow-hidden">
                    <div className="bg-gradient-to-r from-blue-600 to-indigo-600 px-8 py-6">
                        <h2 className="text-2xl font-semibold text-white flex items-center">
                            <User className="h-6 w-6 mr-3" />
                            Supplier Registration Form
                        </h2>
                        <p className="text-blue-100 mt-2">Please fill in all required information to register a new supplier</p>
                    </div>

                    <div className="p-8 space-y-8">
                        {/* Basic Information Section */}
                        <div className="space-y-6">
                            <div className="border-b border-gray-200 pb-4">
                                <h3 className="text-lg font-semibold text-gray-900 flex items-center">
                                    <Building2 className="h-5 w-5 mr-2 text-blue-600" />
                                    Basic Information
                                </h3>
                            </div>

                            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                                {/* Supplier Name */}
                                <div className="md:col-span-2">
                                    <label className="block text-sm font-medium text-gray-700 mb-2">
                                        Supplier Name *
                                    </label>
                                    <input
                                        type="text"
                                        name="name"
                                        value={formData.name}
                                        onChange={handleInputChange}
                                        className={`w-full px-4 py-3 rounded-lg border-2 transition-all duration-200 ${errors.name ? 'border-red-300 focus:border-red-500' : 'border-gray-300 focus:border-blue-500'
                                            } focus:outline-none focus:ring-2 focus:ring-blue-200`}
                                        placeholder="Enter supplier company name"
                                    />
                                    {errors.name && <p className="text-red-500 text-sm mt-1">{errors.name}</p>}
                                </div>

                                {/* Account Number */}
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">
                                        Account Number *
                                    </label>
                                    <input
                                        type="text"
                                        name="accNo"
                                        value={formData.accNo}
                                        onChange={handleInputChange}
                                        className={`w-full px-4 py-3 rounded-lg border-2 transition-all duration-200 ${errors.accNo ? 'border-red-300 focus:border-red-500' : 'border-gray-300 focus:border-blue-500'
                                            } focus:outline-none focus:ring-2 focus:ring-blue-200`}
                                        placeholder="Enter account number"
                                    />
                                    {errors.accNo && <p className="text-red-500 text-sm mt-1">{errors.accNo}</p>}
                                </div>

                                {/* Type */}
                                
                                    <div className="flex items-center gap-4">
                                        <label className="block text-sm font-medium text-gray-700 mb-2">Supplier Type</label>
                                        <select
                                            value={formData.type}
                                            onChange={(e) => {
                                                const selectedId = parseInt(e.target.value);
                                                const selectedType = types.find(t => t.id === selectedId);
                                                setFormData(prev => ({
                                                    ...prev,
                                                    typeId: selectedId,
                                                    type: selectedType ? selectedType.name : ''
                                                }));
                                            }}
                                            className="flex-1 border border-gray-300 rounded px-3 py-1.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
                                        >
                                            <option value={0}>Select Type</option>
                                            {types
                                                .filter(type => type?.id != null && type?.name)
                                                .map(type => (
                                                    <option key={type.id} value={type.id}>
                                                        {type.name}
                                                    </option>
                                                ))}
                                        </select>
                                        <button
                                            onClick={() => setShowTypeModal(true)}
                                            className="bg-green-500 hover:bg-green-600 text-white px-3 py-1.5 rounded text-sm font-medium transition-colors"
                                        >
                                            +
                                        </button>
                                    </div>
                            </div>
                        </div>

                        {/* Type Modal */}
                        {showTypeModal && (
                            <div className="fixed inset-0 bg-black/30 flex items-center justify-center z-50">
                                <div className="bg-white rounded-lg shadow-xl w-full max-w-md mx-4">
                                    <div className="bg-gray-600 text-white px-6 py-4 rounded-t-lg flex justify-between items-center">
                                        <h2 className="text-lg font-medium">Add New Type</h2>
                                        <button
                                            onClick={() => setShowTypeModal(false)}
                                            className="text-white hover:text-gray-300 text-xl font-bold"
                                        >
                                            Ã—
                                        </button>
                                    </div>
                                    <div className="p-6">
                                        <label className="block text-sm font-medium text-gray-700 mb-2">Type Name</label>
                                        <input
                                            type="text"
                                            value={newType}
                                            onChange={(e) => setNewType(e.target.value)}
                                            placeholder="Enter type name"
                                            className="w-full border border-gray-300 rounded px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
                                            onKeyPress={(e) => e.key === 'Enter' && handleAddType()}
                                        />
                                    </div>
                                    <div className="border-t border-gray-200 px-6 py-4 flex justify-end gap-3">
                                        <button
                                            onClick={() => setShowTypeModal(false)}
                                            className="bg-gray-500 hover:bg-gray-600 text-white px-4 py-2 rounded text-sm font-medium transition-colors"
                                        >
                                            Cancel
                                        </button>
                                        <button
                                            onClick={handleAddType}
                                            className="bg-blue-500 hover:bg-blue-600 text-white px-4 py-2 rounded text-sm font-medium transition-colors"
                                        >
                                            Add
                                        </button>
                                    </div>
                                </div>
                            </div>
                        )}

                        {/* Address Information Section */}
                        <div className="space-y-6">
                            <div className="border-b border-gray-200 pb-4">
                                <h3 className="text-lg font-semibold text-gray-900 flex items-center">
                                    <MapPin className="h-5 w-5 mr-2 text-blue-600" />
                                    Address Information
                                </h3>
                            </div>

                            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                                {/* Address */}
                                <div className="md:col-span-2">
                                    <label className="block text-sm font-medium text-gray-700 mb-2">
                                        Address *
                                    </label>
                                    <textarea
                                        name="address"
                                        value={formData.address}
                                        onChange={handleInputChange}
                                        rows={3}
                                        className={`w-full px-4 py-3 rounded-lg border-2 transition-all duration-200 ${errors.address ? 'border-red-300 focus:border-red-500' : 'border-gray-300 focus:border-blue-500'
                                            } focus:outline-none focus:ring-2 focus:ring-blue-200`}
                                        placeholder="Enter complete address"
                                    />
                                    {errors.address && <p className="text-red-500 text-sm mt-1">{errors.address}</p>}
                                </div>

                                {/* Location */}
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">
                                        Location *
                                    </label>
                                    <input
                                        type="text"
                                        name="location"
                                        value={formData.location}
                                        onChange={handleInputChange}
                                        className={`w-full px-4 py-3 rounded-lg border-2 transition-all duration-200 ${errors.location ? 'border-red-300 focus:border-red-500' : 'border-gray-300 focus:border-blue-500'
                                            } focus:outline-none focus:ring-2 focus:ring-blue-200`}
                                        placeholder="Enter city/location"
                                    />
                                    {errors.location && <p className="text-red-500 text-sm mt-1">{errors.location}</p>}
                                </div>

                                {/* Post Office */}
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">
                                        Post Office
                                    </label>
                                    <input
                                        type="text"
                                        name="post"
                                        value={formData.post}
                                        onChange={handleInputChange}
                                        className="w-full px-4 py-3 rounded-lg border-2 border-gray-300 focus:border-blue-500 focus:outline-none focus:ring-2 focus:ring-blue-200 transition-all duration-200"
                                        placeholder="Enter post office"
                                    />
                                </div>

                                {/* PIN Code */}
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">
                                        PIN Code *
                                    </label>
                                    <input
                                        type="text"
                                        name="pin"
                                        value={formData.pin}
                                        onChange={handleInputChange}
                                        maxLength={6}
                                        className={`w-full px-4 py-3 rounded-lg border-2 transition-all duration-200 ${errors.pin ? 'border-red-300 focus:border-red-500' : 'border-gray-300 focus:border-blue-500'
                                            } focus:outline-none focus:ring-2 focus:ring-blue-200`}
                                        placeholder="Enter 6-digit PIN"
                                    />
                                    {errors.pin && <p className="text-red-500 text-sm mt-1">{errors.pin}</p>}
                                </div>
                            </div>
                        </div>

                        {/* Contact Information Section */}
                        <div className="space-y-6">
                            <div className="border-b border-gray-200 pb-4">
                                <h3 className="text-lg font-semibold text-gray-900 flex items-center">
                                    <Phone className="h-5 w-5 mr-2 text-blue-600" />
                                    Contact Information
                                </h3>
                            </div>

                            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                                {/* Phone Number */}
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">
                                        Phone Number *
                                    </label>
                                    <input
                                        type="tel"
                                        name="phone"
                                        value={formData.phone}
                                        onChange={handleInputChange}
                                        className={`w-full px-4 py-3 rounded-lg border-2 transition-all duration-200 ${errors.phone ? 'border-red-300 focus:border-red-500' : 'border-gray-300 focus:border-blue-500'
                                            } focus:outline-none focus:ring-2 focus:ring-blue-200`}
                                        placeholder="Enter 10-digit phone number"
                                    />
                                    {errors.phone && <p className="text-red-500 text-sm mt-1">{errors.phone}</p>}
                                </div>

                                {/* WhatsApp Number */}
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">
                                        WhatsApp Number
                                    </label>
                                    <input
                                        type="tel"
                                        name="whatsapp"
                                        value={formData.whatsapp}
                                        onChange={handleInputChange}
                                        className="w-full px-4 py-3 rounded-lg border-2 border-gray-300 focus:border-blue-500 focus:outline-none focus:ring-2 focus:ring-blue-200 transition-all duration-200"
                                        placeholder="Enter WhatsApp number"
                                    />
                                </div>

                                {/* Email */}
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">
                                        Email Address
                                    </label>
                                    <input
                                        type="email"
                                        name="email"
                                        value={formData.email}
                                        onChange={handleInputChange}
                                        className={`w-full px-4 py-3 rounded-lg border-2 transition-all duration-200 ${errors.email ? 'border-red-300 focus:border-red-500' : 'border-gray-300 focus:border-blue-500'
                                            } focus:outline-none focus:ring-2 focus:ring-blue-200`}
                                        placeholder="Enter email address"
                                    />
                                    {errors.email && <p className="text-red-500 text-sm mt-1">{errors.email}</p>}
                                </div>
                            </div>
                        </div>

                        {/* Business Information Section */}
                        <div className="space-y-6">
                            <div className="border-b border-gray-200 pb-4">
                                <h3 className="text-lg font-semibold text-gray-900 flex items-center">
                                    <FileText className="h-5 w-5 mr-2 text-blue-600" />
                                    Business Information
                                </h3>
                            </div>

                            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                                {/* GST Number */}
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">
                                        GST Number
                                    </label>
                                    <input
                                        type="text"
                                        name="gstNo"
                                        value={formData.gstNo}
                                        onChange={handleInputChange}
                                        className={`w-full px-4 py-3 rounded-lg border-2 transition-all duration-200 ${errors.gstNo ? 'border-red-300 focus:border-red-500' : 'border-gray-300 focus:border-blue-500'
                                            } focus:outline-none focus:ring-2 focus:ring-blue-200`}
                                        placeholder="Enter GST number"
                                    />
                                    {errors.gstNo && <p className="text-red-500 text-sm mt-1">{errors.gstNo}</p>}
                                </div>

                                {/* Discount Percentage */}
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">
                                        <Percent className="h-4 w-4 inline mr-1" />
                                        Discount Percentage
                                    </label>
                                    <input
                                        type="number"
                                        name="discPercentage"
                                        value={formData.discPercentage}
                                        onChange={handleInputChange}
                                        min="0"
                                        max="100"
                                        step="0.01"
                                        className="w-full px-4 py-3 rounded-lg border-2 border-gray-300 focus:border-blue-500 focus:outline-none focus:ring-2 focus:ring-blue-200 transition-all duration-200"
                                        placeholder="Enter discount percentage"
                                    />
                                </div>
                            </div>
                        </div>

                        {/* Submit Button */}
                        <div className="flex justify-center pt-6">
                            <button
                                type="button"
                                onClick={handleSubmit}
                                disabled={isSubmitting}
                                className="bg-gradient-to-r from-blue-600 to-indigo-600 text-white px-12 py-4 rounded-lg font-semibold text-lg shadow-lg hover:shadow-xl transform hover:scale-105 transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed disabled:transform-none"
                            >
                                {isSubmitting ? (
                                    <span className="flex items-center">
                                        <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                                            <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                                            <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                                        </svg>
                                        Processing...
                                    </span>
                                ) : (
                                    'Register Supplier'
                                )}
                            </button>
                        </div>
                    </div>
                </div>

                {/* Footer */}
                <div className="text-center mt-8">
                    <p className="text-gray-500 text-sm">
                        Â© 2025 MediCorp Solutions. All rights reserved.
                    </p>
                </div>
            </div>
        </div>
    );
};

export default SupplierForm;